body {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  max-width: 100%;
  background: var(--baseD);
  overflow: hidden;
  user-select: none !important;
}

/* ############################### */

*,
::after,
::before {
  box-sizing: border-box;
}

/* ############################### */

body {
  &[data-aos-easing=sleek] [data-aos] {
    transition-timing-function: var(--sleek);
  }

  &::-webkit-scrollbar {
    width: 10px;
  }

  &::-webkit-scrollbar-track {
    background: var(--baseD);
  }

  &::-webkit-scrollbar-thumb {
    background: var(--basesecondaryD);
    border-radius: 10px;

    &:hover {
      background: var(--accentD);
    }

    &:active {
      background: var(--accentsecondaryD);
    }
  }
}

@font-face {
  font-family: InterTight;
  src: url(../../assets/fonts/InterTight-VariableFont_wght.ttf) format("truetype-variations");
  font-weight: 1 1000;
}

@font-face {
  font-family: JetBrains Mono;
  src: url(../../assets/fonts/JetBrainsMono[wght].ttf) format("truetype-variations");
  font-weight: 1 1000;
}

.material-symbols-rounded {
  vertical-align: middle;
  font-variation-settings:
    'FILL' 0,
    'wght' 400,
    'GRAD' 0,
    'opsz' 60
}

:root {
  --baseD: #212728;
  --baseL: rgba(252, 242, 233, 1);
  --alphabaseD: #212728ee;
  --alphabaseL: rgba(252, 242, 233, 0.7);
  --alpha2baseD: rgba(24, 24, 24, 0.3);
  --alpha2baseL: rgba(252, 242, 233, 0.3);
  --basesecondaryD: rgb(63, 63, 63);
  --basesecondaryL: rgb(199, 194, 189);
  --textD: rgb(255, 255, 255);
  --textL: rgb(19, 19, 19);
  --textsecondaryD: rgb(202, 202, 202);
  --textsecondaryL: rgb(40, 40, 40);
  --red1: rgb(241, 99, 99);
  --accentD: rgb(79, 150, 197);
  --accentL: rgb(252, 193, 125);
  --redsecondary: rgb(226, 133, 133);
  --accentsecondaryD: rgb(151, 189, 216);
  --accentsecondaryL: rgb(255, 220, 191);
  --accentfillD: rgb(29, 31, 32);
  --accentfillL: rgb(255, 252, 250);
  --textaccentD: rgb(94, 186, 255);
  --textaccentL: rgb(255, 167, 94);
  --black: black;
  --blackfL: white;
  --blacktD: black;
  --white: white;
  --textfL: rgb(19, 19, 19);
  --texttD: rgb(255, 255, 255);
  --basefL: rgb(252, 242, 233);
  --basetD: rgb(24, 24, 24);
  --boxshadowD: rgba(0, 0, 0, 0) 0 0 0px;
  --boxshadowL: rgba(0, 0, 0, 0.25) 0 2px 12px;
  //--loaderD: url("../../img/local/spinnerD.gif");
  //--loaderL: url("../../img/local/spinnerL.gif");
  --fontDisplay: InterTight;
  --fontSecondary: JetBrains Mono;
  --sleek: cubic-bezier(0.03, 0.77, 0.34, 1);
  --accel: cubic-bezier(0.72, 0.06, 0.96, 0.57);
  --anchorhead: cubic-bezier(0.24, 0.01, 0.01, 1);
  --bounce: cubic-bezier(0.57, 0.07, 0.41, 1.95);
  --quickBounce: cubic-bezier(0, 1.81, .41, .95);

  --brightnessD: brightness(1.2);
  --brightnessL: brightness(.8);
}

[data-theme=light] {
  --baseD: var(--baseL);
  --alphabaseD: var(--alphabaseL);
  --alpha2baseD: var(--alpha2baseL);
  --basesecondaryD: var(--basesecondaryL);
  --textD: var(--textL);
  --textsecondaryD: var(--textsecondaryL);
  --accentD: var(--accentL);
  --accentsecondaryD: var(--accentsecondaryL);
  --accentfillD: var(--accentfillL);
  --textaccentD: var(--textaccentL);
  --blackfL: var(--blacktD);
  --black: var(--white);
  --textfL: var(--texttD);
  --basefL: var(--basetD);
  --boxshadowD: var(--boxshadowL);
  --loaderD: var(--loaderL);

  --brightnessD: var(--brightnessL)
}

a {
  text-decoration: none !important;
  color: initial;

  &:link,
  &:visited {
    text-decoration: none !important;
    color: currentColor;
  }
}

.scene {
  min-height: 100vh;
  min-width: 100vw;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.SCRIPT-sceneDraw {
  display: block;
}

.SCRIPT-sceneHide {
  display: none;
}

.SCRIPT-sceneInUp {
  animation: sceneInUp .5s forwards var(--anchorhead);

  @keyframes sceneInUp {
    from {
      transform: translateY(100vh);
      opacity: 0;
    }

    to {
      transform: translateY(0vh);
      opacity: 1;
    }
  }
}

.SCRIPT-sceneInScaleUp {
  animation: sceneInScaleUp .5s forwards var(--sleek);

  @keyframes sceneInScaleUp {
    from {
      transform: scale(.5);
      opacity: 0;
    }

    to {
      transform: scale(1);
      opacity: 1;
    }
  }
}

.SCRIPT-sceneInScaleDown {
  animation: sceneInScaleDown .5s forwards var(--sleek);

  @keyframes sceneInScaleDown {
    from {
      transform: scale(1.2);
      opacity: 0;
    }

    to {
      transform: scale(1);
      opacity: 1;
    }
  }
}

.SCRIPT-sceneOutLeft {
  animation: sceneOutLeft .5s forwards var(--accel);

  @keyframes sceneOutLeft {
    from {
      transform: translateX(0vw);
      opacity: 1;
    }

    to {
      transform: translateX(-100vw);
      opacity: 0;
    }
  }
}

.SCRIPT-sceneOutDown {
  animation: sceneOutDown .5s forwards var(--accel);

  @keyframes sceneOutDown {
    from {
      transform: translateY(0vh);
      opacity: 1;
    }

    to {
      transform: translateY(100vh);
      opacity: 0;
    }
  }
}

.SCRIPT-sceneOutScaleUp {
  animation: sceneOutScaleUp .5s forwards var(--accel);

  @keyframes sceneOutScaleUp {
    from {
      transform: scale(1);
      opacity: 1;
    }

    to {
      transform: scale(1.5);
      opacity: 0;
    }
  }
}

.SCRIPT-sceneOutScaleDown {
  animation: sceneOutScaleUp .5s forwards var(--accel);

  @keyframes sceneOutScaleUp {
    from {
      transform: scale(1);
      opacity: 1;
    }

    to {
      transform: scale(.5);
      opacity: 0;
    }
  }
}

@keyframes elemEnter-scale {
  from {
    opacity: 0;
    transform: scale(.5);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes elemEnter-slideRight {
  from {
    opacity: 0;
    transform: translateX(-3em);
  }

  to {
    opacity: 1;
    transform: translateX(0em);
  }
}

@keyframes elemEnter-slideLeft {
  from {
    opacity: 0;
    transform: translateX(3em);
  }

  to {
    opacity: 1;
    transform: translateX(0em);
  }
}

@keyframes elemEnter-slideDown {
  from {
    opacity: 0;
    transform: translateY(-1em);
  }

  to {
    opacity: 1;
    transform: translateX(0em);
  }
}
@keyframes elemEnter-slideUp {
  from {
    opacity: 0;
    transform: translateY(1em);
  }

  to {
    opacity: 1;
    transform: translateX(0em);
  }
}

.ghostLoader {
  z-index: -99;
  opacity: 0;
}


.clr-picker {
  border-radius: 20px !important;
  animation: elemEnter-scale .2s forwards var(--quickBounce);
}
.clr-field>button {
  display: none;
}

.clr-close {
  transition: all .1s;
  font-family: var(--fontSecondary) !important;
  width: 80%;
  height: 50px !important;
  border-radius: 10px !important;
  background: rgba(0, 0, 0, 0) !important;
  border: 1px solid white !important;
  font-size: 1.5em !important;
  background-clip: text;

  &:hover {
    @supports (color: color-contrast()) {
      color: color-contrast(var(--regen-color) vs white, black);
    }
    background: var(--regen-color) !important;
    border: 1px solid var(--regen-color) !important;
  }
}